* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: #02558b;
  font-family: Roboto, sans-serif;
}

.main__container {
  height: 80vh;
  width: 50%;
  justify-content: center;
  align-items: center;
  margin: 50px auto;
  display: flex;
}

.chat__container {
  width: 100%;
  height: 100%;
  background-color: #dfdce3;
  border: 4px solid #000;
  border-radius: 25px;
  flex-direction: column;
  display: flex;
}

@media only screen and (min-width: 764px) {
  .chat__container {
    width: 540px;
  }
}

.header {
  height: 40px;
  width: 100%;
  border-bottom: 4px solid #000;
}

.root {
  height: 100%;
  background-color: #fff;
  padding: 10px;
}

.low__section {
  width: 100%;
  height: 12%;
  border-top: 4px solid #000;
  display: block;
}

